@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-body {
  background: url(/eth-calculator/static/media/background.0dc55042.jpeg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}
.main-content {
  align-self: center;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.header {
  grid-gap: 2rem;
  gap: 2rem;
  display: flex;
  justify-content: center;
}

.panel-wrapper {
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: rgba(0, 0, 0, 0.3);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  padding: 2rem;
  border-radius: 4rem;
  grid-gap: 2rem;
  gap: 2rem;
}

.input-wrapper {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  grid-gap: 2rem;
  gap: 2rem;
  width: 100%;
  color: white;
}

input {
  border: none;
  -webkit-appearance: none;
}

input:focus {
  outline: none;
}

.input-field {
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border-radius: 1rem;
  padding: 0.5rem;
  font-family: 'Montserrat', sans-serif;
}

.c-button {
  background: #ffffff93;
  border-radius: 1rem;
  border: 0;
  color: rgb(44, 44, 44);
  cursor: pointer;
  font-weight: bold;
  outline: none;
  padding: 1rem 1rem;
  position: relative;
  text-transform: uppercase;
}

